.ProjectsContainer {
  
  ul {
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;

    li {
      list-style-type: none;
      margin: 1em;

      a {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 300px;
        height: 200px;
      }
    }
  }
}
