@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center;background-color:#1f0318;font-family:'Raleway', sans-serif;color:#f5e3e0;min-height:100vh;max-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.App header{padding-top:15vh;-webkit-flex:0 0 auto;flex:0 0 auto}.App footer{-webkit-flex:0 0 auto;flex:0 0 auto}.App .body-container{margin:10vh;-webkit-flex:1 1 auto;flex:1 1 auto;overflow-y:auto}a{color:#10ffcb;text-decoration:none}a:hover{color:#d282a6}.links-wrapper{list-style:none}*{scrollbar-width:thin;scrollbar-color:#f5e3e0 #1f0318}*::-webkit-scrollbar{width:12px}*::-webkit-scrollbar-track{background:#1f0318}*::-webkit-scrollbar-thumb{background-color:#f5e3e0;border-radius:20px;border:3px solid #1f0318}

.Footer ul{display:-webkit-flex;display:flex;-webkit-align-content:flex-end;align-content:flex-end;-webkit-align-items:flex-end;align-items:flex-end;-webkit-justify-content:center;justify-content:center;margin-top:auto;margin-bottom:1px}

.LinkContainer li a{margin:.25em}.links-wrapper{padding:0}

nav{margin:10}nav a{font-size:2rem;margin:0 2em}@media screen and (max-width: 865px){nav{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}}

.About{max-height:100%;font-size:2rem;margin:auto}

.ProjectsContainer ul{padding:0;margin:0;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.ProjectsContainer ul li{list-style-type:none;margin:1em}.ProjectsContainer ul li a{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:300px;height:200px}

